/*  -- ANIMATION CLASSES -- */
.fade-appear{
  opacity: 0;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.fade-appear-active{
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}
.fadeTopBar-appear{
  opacity: 0;  
  -webkit-transition: all 0.3s ease-out;  
  transition: all 0.3s ease-out;
}
.fadeTopBar-appear-active{
  opacity: 1;
}

.textMove-enter-active{
  -webkit-animation: textFade 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
          animation: textFade 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.textMove-exit-active{
  -webkit-animation: textFade 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
          animation: textFade 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.loader {
  color: #eb3349;
  font-size: 90px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin: 72px auto;
  position: relative;
  align-self: center;
  justify-self: center;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
  animation: load6 1.7s infinite ease, round 1.7s infinite ease;
}
